<template>
    <div class="adt-my-modal">
        <Modal
            :width="width"
            v-model="isShowModal"
            :title="title"
            :mask-closable="false"
            :footer-hide="true"
            @on-cancel="handleCancel"
            @on-visible-change="modalChange">
            <slot></slot>
            <div class="modal-footer">
                <Button @click="handleFeatures" shape="circle" v-if="isShowThirdBtn">{{ showThirdTitle }}</Button>
                <Button @click="handleCancel" shape="circle" v-if="showCancelBtn">{{ cancelText ? cancelText : $t('common.cancel') }}</Button>
                <Button @click="handleConfirm" shape="circle" type="primary">{{ okText ? okText : $t('common.ok') }}</Button>
            </div>
        </Modal>
    </div>
</template>

<script>
export default {
    name: 'AdtMyModal',
    props: {
        showModal: {
            type: Boolean,
            default: false
        },
        width: {
            type: String,
            default: '450' // 如果是新增模态框有两列，width传650；如果是审核模态框，width传800
        },
        title: {
            type: String
        },
        okText: {
            type: String
        },
        cancelText: {
            type: String
        },
        showCancelBtn: {
            type: Boolean,
            default: true
        },
        isShowThirdBtn: {
            type: Boolean,
            default: false
        },
        showThirdTitle: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            loading: true,
            isShowModal: false
        }
    },
    watch: {
        showModal: {
            handler (to, from) {
                this.isShowModal = to
            },
            immediate: true
        }
    },
    methods: {
        handleFeatures () {
            this.$emit('handleFeatures')
        },
        handleConfirm () {
            this.$emit('on-ok')
        },
        handleCancel () {
            this.$emit('on-cancel')
            this.isShowModal = false
        },
        modalChange (changeStatus) {
            //  将模态框当前的状态返回给父组件 同步父组件里面的值
            this.$emit('on-visible-change', changeStatus)
        }
    }
}
</script>

<style lang="scss" scoped>
    /deep/.ivu-modal-header{
        border: none;
        padding: 20px 30px;
    }
    .modal-footer {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 60px;
        button + button {
            margin-left: 12px;
        }
    }
    /deep/.ivu-modal-header-inner{
        color: #666;
        font-weight: 500;
    }
    /deep/.ivu-modal-header{
        position: relative;
    }
    /deep/.ivu-modal-header-inner::after{
        position: absolute;
        left: 0;
        top: 23px;
        content: '';
        height: 14px;
        border: 2px solid $light-color;

    }
</style>
