<template>
  <div class="adt-svg-icon">
    <svg :class="svgClass" aria-hidden="true">
      <use :xlink:href="iconName" />
    </svg>
  </div>
</template>

<script>
export default {
  name: "AdtSvgIcon",
  props: {
    iconClass: {
      type: String,
      required: true
    }
  },
  computed: {
    iconName() {
      return `#icon-andaotongcaidanICON_${this.iconClass}`;
    },
    svgClass() {
      if (this.iconClass) {
        return "icon " + this.iconClass;
      } else {
        return "icon";
      }
    }
  },
  data() {
    return {};
  },
  watch: {},
  methods: {}
};
</script>

<style lang="scss" scoped>
.adt-svg-icon {
  width: 100%;
  height: 100%;
  .icon {
    width: 100%;
    height: 100%;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }
}
</style>
